.cards-list {
  @media screen and (min-width: 768px) {
    background-image: url(../images/tablet-img/bg-body-768.png);
    background-repeat: no-repeat;
    margin-top: -40px;
  }
  @media screen and (min-width: 1280px) {
    background-image: url(../images//desktop-img/bg-body.png);
    background-repeat: no-repeat;
    margin-top: -40px;
  }
}

.list-card-main {
  display: flex;
  flex-wrap: wrap;
  column-gap: 14px;
  row-gap: 40px;
  @media screen and (min-width: 1280px) {
    column-gap: 60px;
    row-gap: 50px;
  }
}

.item-card-main {
  position: relative;
  width: 128px;
  @media screen and (min-width: 768px) {
    width: 190px;
  }
}

.item-card-main::before {
  content: '';
  position: absolute;
  top: -10px;
  right: -10px;
  border: 1px solid rgba(220, 86, 197, 0.3);
  border-radius: 50px 0;
  width: 108px;
  height: 101px;
  @media screen and (min-width: 768px) {
    top: -14px;
    right: -14px;
    width: 153px;
    height: 143px;
  }
}

.img-card {
  border-radius: 50px 0px 50px 0px;
  width: 128px;
  height: 160px;
  @media screen and (min-width: 768px) {
    width: 180px;
    height: 227px;
  }
}

.name-card {
  color: rgb(220, 86, 197);
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 700;
  width: 128px;
  text-align: center;
  @media screen and (min-width: 768px) {
    font-size: 16px;
    width: 184px;
  }
}

.dates-card {
  margin: 0 auto;
  margin-top: 10px;
  width: 85px;
  color: rgb(255, 255, 255);
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 400;
  text-align: center;
  @media screen and (min-width: 768px) {
    font-size: 16px;
    width: 100%;
  }
}

.country-card {
  color: rgb(255, 255, 255);
  font-family: Montserrat;
  font-size: 12px;
  font-weight: 600;
  margin-top: 10px;
  text-align: center;
  @media screen and (min-width: 768px) {
    font-size: 14px;
  }
}
